@import '../../global.scss';

.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: $mainColor;
    position:fixed ;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    &::-webkit-scrollbar {
        display: none;
    }

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    
    
        .left{
            display: flex;
            align-items: center;
    
            .logo{
                font-size: 37px;
                font-weight: 500;
                text-decoration: none;
                margin-right: 40px;
                color: inherit;

                @include mobile{
                    margin: 0px;
                    font-size: 28px;
                       
                }
            }
    
            .itemConteiner{
                display: flex;
                align-items: center;
                margin-left: 30px;
                
                @include mobile{

                    display: none;
                       
                }
    
                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                }
    
                span{
                    font-size: 15px;
                    font-weight: 500;
                }
            }
    
    
        }
    
    
        .right{
            
            
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
    
                span{
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left ;
                    transition: all 1.5s ease;
                }
            }
        }
    
    }


    &.active{
        background-color: $mainColor;
        color:white; 

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}

